/* 헤더 전체 스타일 */
.header {
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  z-index: 1000;
}
.padding {
  display: flex;
  align-items: center;
  justify-content: space-between; /* 왼쪽 요소와 오른쪽 요소를 양 끝 정렬 */
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 1.875rem;
}
/* 햄버거 메뉴 */
.gnb {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 30px;
  height: 100%;
  cursor: pointer;
}

.gnb .line {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 2px;
  background-color: white;
  border-radius: 5px;
}
.gnb .line::before,
.gnb .line::after {
  content: "";
  position: absolute;
  display: inline-block;
  width: 20px;
  height: 2px;
  left: 5px;
  background-color: white;
  border-radius: 5px;
  transition: transform 0.3s ease;
}
.gnb .line::before {
  top: -6px;
}
.gnb .line::after {
  top: 6px;
}
.gnb:hover .line::before {
  transform: translateX(-9px) rotate(-45deg);
}
.gnb:hover .line::after {
  transform: translateX(-9px) rotate(45deg);
}
/* 로고 */
.logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (max-width: 767px) {
  /* 모바일 - 어둡게 */
  .header {
    background-color: rgba(0, 0, 0, 0.3);
  }
}
