.profile-container {
  position: relative;
  z-index: 2; /* 배경 위로 */
  width: 100%;
  height: 100svh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.profile-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 100px 0;
  overflow: scroll;
}

.profile {
  max-width: 1000px;
  width: 100%;
  color: white;
}
.profile img {
  width: 100%;
  margin-bottom: 20px;
}
.profile-info {
  display: flex;
  width: 100%;
  margin-bottom: 100px;
}

.profile-info-left {
  width: 40%;
  padding: 0 20px;
}
.profile-info-left h1 {
  margin: 0;
}
.profile-info-left div {
  color: rgba(255, 255, 255, 0.8);
}
.profile-info-left div b {
  color: rgba(255, 255, 255, 1);
}
.profile-info-left .sns {
  display: inline-block;
  padding: 0;
}
.profile-info-left .sns li {
  float: left;
  height: 32px;
  margin: 0 3px;
}
.profile-info-left .sns img {
  opacity: 0.7;
}
.profile-info-left .sns img:hover {
  opacity: 1;
}

.profile-info-right {
  width: 60%;
  padding: 0 20px;
}
.profile-info-right h3 {
  margin: 0 0 20px 0;
}
.profile-info-right .contents {
  display: flex;
  opacity: 0.6;
  font-size: 14px;
}
.profile-info-right .contents .year {
  flex: 1;
  text-align: end;
  margin-right: 16px;
}
.profile-info-right .contents .content {
  flex: 2;
}

.members {
  width: 100%;
}
.slide {
  display: flex;
  width: 100%;
  height: fit-content;
  margin-bottom: 50px;
}

.member-card {
  background-color: #e9e9e9;
}
.member-card img {
  width: 100%;
}

.member-info {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.member-info .cover {
  position: absolute;
  width: 100%;
  bottom: 100%;
}
.member-info .nameplate {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: black;
  width: 70%;
  margin: 0 5%;
  padding: 10%;
  border: 1px solid #c8c8c8;
}
.member-info .nameplate h3 {
  margin: 0;
}
.member-info .nameplate div {
  font-size: 12px;
}
.member-insta {
  margin: 15px;
  filter: brightness(0.8);
}
.member-insta:hover {
  filter: brightness(0.6);
}

.swiper-scrollbar {
  display: block !important;
  position: static !important;
  background: rgba(255, 255, 255, 0.15) !important;
  margin: 0 auto;
  max-width: 670px !important;
  width: 100% !important;
  height: 8px !important;
  border-radius: 0 !important;
}

.swiper-scrollbar-drag {
  background: rgba(255, 255, 255, 0.5) !important;
  width: 50px !important;
  height: 100% !important;
  border-radius: 0 !important;
}
.swiper-scrollbar-drag:hover {
  background: rgba(255, 255, 255, 1) !important;
}

@media screen and (max-width: 767px) {
  .profile-container {
    height: fit-content;
  }
  .profile-wrapper {
    margin: 100px 0 0 0;
    overflow: hidden;
  }
  .profile-info {
    flex-direction: column;

    width: 100%;
    margin-bottom: 100px;
  }

  .profile-info-left {
    width: fit-content;
  }
  .profile-info-right {
    width: fit-content;
    padding: 0 20px;
    margin: 20px 0;
  }
}
