.detail-container {
  position: relative;
  z-index: 2; /* 배경 위로 */
  width: 100%;
  height: 100svh;
  display: flex;
  justify-content: center;
}

.detail-container .album {
  display: flex;
  flex-direction: column;
  margin: 100px 0;
  padding: 0;
  overflow: scroll;
}

.detail-container .album-imgAndTitle {
  display: flex;
  align-items: center;
}
.detail-container .album-imgAndTitle img {
  width: 400px;
  object-fit: cover;
}

.detail-container .album-imgAndTitle h1 {
  color: #ffffff;
  font-size: 72px;
  font-weight: 700;
  padding: 0 20px 0px 20px;
  margin: 0;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

.detail-container .album-links {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
}
.detail-container .album-linksInfo {
  display: flex;
  width: 100%;
  color: #ffffff;
  font-size: 18px;
  padding-bottom: 10px;
  border-bottom: solid 1px white;
}
.detail-container .album-linksInfo img {
  margin-right: 15px;
  width: 18px;
  height: fit-content;
}

.detail-container .album-link {
  display: inline-block; /* 각 항목을 인라인으로 */
  width: 100%;
  list-style: none; /* 기본 목록 스타일 제거 */
  padding: 0;
}

.detail-container .album-link li {
  float: left;
  margin-right: 15px;
  display: flex;
  align-items: center;
}

.detail-container .album-link li a {
  color: #ffffff; /* 기본 텍스트 색상 */
  font-size: 14px;
  opacity: 0.7;
  vertical-align: middle;
  display: flex;
  align-items: center;
}

.detail-container .album-link li a:hover {
  opacity: 1; /* 마우스 오버 시 색상 */
}
.detail-container .album-link li a img {
  width: 50px;
  margin-right: 10px;
}
.detail-container .script {
  max-width: 800px;
  margin: 50px 0;
}

.detail-container .script p {
  font-size: 14px;
  font-weight: 300;
  line-height: 1.6;
  margin: 0px;
}

.detail-container p {
  color: rgba(255, 255, 255, 0.9); /* 일반 텍스트의 투명도를 0.7로 설정 */
}

.detail-container p strong {
  color: rgba(255, 255, 255, 1); /* strong 태그는 투명도 1 */
}

@media screen and (max-width: 767px) {
  /* 모바일 - 슬라이드 한개, 앨범커버 크게, 타이틀 밑으로 */
  .detail-container {
    height: fit-content;
  }
  .detail-container .album {
    width: calc(100% - 60px);

    margin: 100px 0 0 0;
    padding: 0 30px;
    overflow: hidden;
  }
  .detail-container .album-imgAndTitle {
    flex-direction: column;
    align-items: flex-start;
  }
  .detail-container .album-imgAndTitle img {
    width: 100%;
  }

  .detail-container .album-imgAndTitle h1 {
    font-size: 48px;
    padding: 0;
  }
}
