.video-container {
  position: relative;
  z-index: 2; /* 배경 위로 */
  width: 100%;
  height: 100svh;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: scroll;
}
.video-wrapper {
  position: relative;
  max-width: 900px;
  width: 100%;
  aspect-ratio: 16/9; /* 16:9 비율 유지 (다른 비율도 가능) */
  margin: 100px 0 50px 0;
}
.video-wrapper .video {
  width: 100%; /* 부모 요소의 100% 너비 */
  aspect-ratio: 16/9; /* 16:9 비율 유지 (다른 비율도 가능) */
}
.video-wrapper .video-cover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(
    to right,
    black 0%,
    rgba(0, 0, 0, 0.8) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
}
.video-wrapper .video-cover h2 {
  margin: 0 50px;
  padding: 0;
  font-size: 2.5rem;
}
.video-wrapper .video-cover button {
  display: flex;
  align-items: center;
  width: fit-content;
  background: red;
  border: 0;
  color: white;
  padding: 10px 18px 8px;
  margin: 0 50px;
  font-size: 14px;
  cursor: pointer;
}
.video-wrapper .video-cover-none {
  display: none;
}

.thumbnails {
  width: 100%;
}
.slide {
  display: flex;
  width: 100%;
  height: fit-content;
  margin-bottom: 50px;
}

.video-container .swiper-slide img {
  width: 100%;
}

@media screen and (max-width: 767px) {
  /* 모바일 - 비디오커버 없애고 인포 밑으로 */
  .video-wrapper .video-cover {
    position: static;
    width: fit-content;
    height: fit-content;
    background: none;
    padding: 20px 0;
  }
  .video-wrapper .video-cover h2 {
    margin: 0 30px;
    padding: 0;
    font-size: 2rem;
  }
  .video-wrapper .video-cover button {
    display: none;
  }
  .video-wrapper .video-cover-none {
    display: block;
    position: static;
    width: fit-content;
    height: fit-content;
    background: none;
    padding: 20px 0;

    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
  }
  .video-wrapper .video-cover-none h2 {
    margin: 0 30px;
    padding: 0;
    font-size: 2rem;
  }
  .video-wrapper .video-cover-none button {
    display: none;
  }
}
