.disco-container {
  position: relative;
  z-index: 2; /* 배경 위로 */
  width: 100%;
  height: 100svh;
  display: flex;
  justify-content: center;
}
.disco-container .contents {
  max-width: 800px;
  margin: 100px 0;
  overflow: scroll;
}

.disco-container .contents ul {
  list-style: none;
  display: inline-block;
  width: 100%;
  padding: 0;
  margin: 0;
}
.disco-container .contents ul li {
  float: left;
  width: 33%;
  padding: 1%;
  box-sizing: border-box; /* 패딩을 width에 포함 */
}
.disco-container .contents ul li img {
  width: 100%;
}

@media screen and (max-width: 767px) {
  /* 모바일 -  한 줄에 2개 */
  .disco-container .contents ul li {
    float: left;
    width: 50%;
    padding: 1%;
    box-sizing: border-box; /* 패딩을 width에 포함 */
  }
  .disco-container {
    height: fit-content;
  }
  .disco-container .contents {
    margin: 100px 0 0 0;
    overflow: hidden;
  }
}
