.main-container {
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100vw; /* 화면 전체 너비 */
  height: 100vh;
  min-height: 100vh; /* 화면 전체 높이 */
  display: flex;
  flex-direction: column;
}
.main-container.shifted {
  transform: scale(0.8) translateX(32%); /* 오른쪽으로 이동 */
  filter: blur(2px);
  box-shadow: -16px 24px 10px rgba(0, 0, 0, 0.5);
  z-index: -1;
}

.main-container::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh; /* 화면 높이를 동적으로 조정 */
  background-image: url("../public/images/background.jpg"); /* 배경 이미지 경로 */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  filter: brightness(0.5); /* 배경을 어둡게 */
}

@media screen and (max-width: 767px) {
  /* 모바일 - 메인화면 오른쪽으로 더 밀기, 풋터까지 스크롤 */
  .main-container.shifted {
    transform: scale(0.8) translateX(90%); /* 오른쪽으로 더 이동 */
  }
  .main-container {
    overflow: scroll;
  }
  .main-container.shifted::before {
    top: var(--scrollTop);
  }
}
