.member-container {
  position: relative;
  z-index: 2; /* 배경 위로 */
  width: 100%;
  height: 100svh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.member-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 100px 0;
  overflow: scroll;
}

/*
profile 부터
profile-info-right .contents .content 까지
profile.css에서 다룸
*/

.members {
  width: 100%;
}
.slide {
  display: flex;
  width: 100%;
  height: fit-content;
  margin-bottom: 50px;
}
.images {
  width: 100%;
}
.images img {
  width: 100%;
}

@media screen and (max-width: 767px) {
  .member-container {
    height: fit-content;
  }
  .member-wrapper {
    margin: 100px 0 0 0;
    overflow: hidden;
  }
}
