/* 헤더 전체 스타일 */
.footer {
  display: flex;
  align-items: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100px;
  z-index: 1000;
}
.footer .padding {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 1.875rem;
}
.footer .container {
  display: inline-block;
  width: 100%;
  padding: 20px 0;
  border-top: solid 0.5px rgba(255, 255, 255, 0.25);
}
.footer .copyright {
  float: left;
  display: flex;
  align-items: center;
  font-size: 9px;
  color: white;
}
.footer .copyright img {
  margin-right: 40px;
  opacity: 0.5;
}
.footer .copyright img:hover {
  opacity: 1;
  cursor: pointer;
}
.footer .container .sns {
  display: inline-block;
  float: right;
  padding: 0;
}
.footer .container .sns li {
  float: left;
  margin: 0 2px;
}
.footer .container .sns img {
  width: 26px;
  opacity: 0.7;
}
.footer .container .sns img:hover {
  opacity: 1;
}

.footer .easterEgg {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.footer .easterEgg .contents {
  height: 80%;
  background-color: #e9e9e9;
}
.footer .easterEgg .contents ul {
  display: flex;
  width: 100%;
  padding: 0;
  margin: 0;
}
.footer .easterEgg .contents ul li {
  width: calc(100% / 6);
  aspect-ratio: 1/1;
}
.footer .easterEgg .video {
  height: calc(100% * 32 / 35);
  aspect-ratio: 9/16; /* 9:16 비율 유지 */
}

@media screen and (max-width: 767px) {
  /* 모바일 - 스크롤가능, 로고, 저작권, sns 세로,가운데정렬 */
  .footer {
    position: relative;
    height: fit-content;
  }
  .footer .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
  }
  .footer .copyright {
    display: flex;
    flex-direction: column;
  }
  .footer .copyright img {
    margin: 10px 0;
  }
  .footer .container .sns {
    margin: 20px 0;
  }
}
