.menu-container {
  display: flex;
  width: 100vw; /* 화면 전체 너비 */
  height: 100svh; /* 화면 전체 높이 */
  position: relative;

  z-index: 0;
}

.menu {
  position: absolute;
  left: 10%;
  transform: translateY(-50%); /* 정확한 세로 가운데 */
  top: 50%; /* 화면 세로 가운데 */
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* 슬라이드 메뉴의 리스트 스타일 */
.menu-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-list li {
  margin: 20px 0;
}

.menu-list li a {
  font-size: 28px;
  font-weight: 1000;
  color: white;
  opacity: 0.7;
}

.menu-list li a:hover {
  opacity: 1;
}

.menu .sns {
  display: inline-block;
  padding: 0;
}
.menu .sns li {
  float: left;
  margin: 0 2px;
}
.menu .sns img {
  width: 26px;
  opacity: 0.7;
}
.menu .sns img:hover {
  opacity: 1;
}

.close-menu {
  position: fixed;
  background: #f23c52;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  left: 36%;
  transform: translateX(-50%) translateY(-50%);
  top: 50%;
}

.close-menu::before,
.close-menu::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 2px;
  border-radius: 5px;
  background-color: white;
  transform: rotate(45deg);
  transition: transform 0.6s ease;
}

.close-menu::after {
  transform: rotate(-45deg);
}

.close-menu:hover::before {
  transform: rotate(225deg);
}
.close-menu:hover::after {
  transform: rotate(135deg);
}

@media screen and (max-width: 767px) {
  /* 모바일 - 메인화면 오른쪽으로 더 민 것에 맞추기 */
  .close-menu {
    left: 80%;
  }
}
